body {
  margin: 0;
  padding: 0;
  background-color: rgb(245, 185, 163);
}

.main-container {
  text-align: center;
}

.activity-container {
  background-color: cornsilk;
  display: grid;
  grid-template-columns: 20% 80%;
  margin: 10px auto;
  padding: 1rem;
  box-shadow: 0px 5px 10px white;
}

.activity-about-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.activity-title {
  font-size: large;
  font-weight: bold;
}

.activity-Month {
  font-size: small;
  font-weight: bolder;
  color: brown;
}

.activity-days-container {
  display: grid;
  grid-template-columns: repeat(10, auto);
  gap: 10px;
}

.done {
  background-color: rgb(245, 185, 163);
}

.individualDays {
  padding: 1rem 1.2rem;
  border: 1px solid black;
  text-align: center;
  font-size: 1.2rem;
}

.individualDays:hover {
  cursor: pointer;
}
